<script setup>
 import { sponsorsDB } from '@/firebase';
 import { getDocs } from 'firebase/firestore';
 import SponsorTile from '@/components/SponsorTile.vue';
</script>

<template>
  <div class="sponsors-wrapper">
    <section id="sponsors">
      <h2 class="header">
        Our Sponsors
      </h2>

      <div class="sponsors-display">
        <div class="message" v-if="sponsors.length == 0">
          Be the first to sponsor us! <br>
          <span class="contact">
            If you are interested then you can find out how to contact us <a href="#contact">here</a>!
          </span>
        </div>

        <SponsorTile v-for="(sponsor, index) in sponsors" :key="index" :data="sponsor"/>
      </div>
    </section>

    <MovingDivider :bottom="true" :index="1"/>
  </div>
</template>

<script>
import MovingDivider from '@/components/MovingDivider.vue'

export default {
    name: "SponsorsView",
    components: {
      MovingDivider
    },
    data()
    {
      return {
        sponsors: []
      }
    },
    async created()
    {
      const sponsors = await getDocs(sponsorsDB)
      sponsors.forEach(sponsor => {
        this.sponsors.push(sponsor.data())
      })
    }
}
</script>

<style lang="scss">
.sponsors-wrapper{
  position: relative;

  background-color: var(--light);
  color: var(--dark);
  // height: 100vh;

  #sponsors{
    padding: var(--section-padding);
    height: 100%;
    max-width: var(--max-width);
    margin: auto;

    .header{
      text-align: left;
    }

    .message{
      font-size: 18pt;
      font-weight: bold;

      .contact{
        font-size: 14pt;
        font-weight: normal;

        a{
          color: var(--dark);
        }
      }
    }

    .sponsors-display
    {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-evenly;
      align-items: center;
    }
  }
}

@media screen and (max-width: 768px) {
  .sponsors-wrapper{ 
    padding-top: 20%;
  }
}
</style>