<template>
  <span class="sponsor-tile">
      <a :href="data.url" target="#">
          <img :src="logoURL">
      </a>
  </span>
</template>

<script>
  import { getStorage, ref, getDownloadURL } from "firebase/storage"

  export default {
      name: "SponsorTile",
      props: {
          data: Object
      },
      data() {
        return {
          logoURL: "",
        }
      },
      created()
      {
        if(this.$props.data.logo.length > 0)
         getDownloadURL(ref(getStorage(), this.$props.data.logo)).then((url) => this.logoURL = url)
      }
  }
</script>

<style lang="scss" scoped>
img{
  height: 5em;
}
</style>