<template>
  <div class="member-tile">
    <div class="profile-wrapper" @click="email">
        <img class="member-image" :src="imageURL">
    </div>
    <div class="member-info">
        <div class="name">{{member.first_name}} {{member.last_name}}</div>
        <div class="title">{{member.role}}</div>
    </div>
  </div>
</template>

<script>
import { getStorage, ref, getDownloadURL } from "firebase/storage"

export default {
    name: "MemberTile",
    props: {
        member: Object
    },
    data() {
        return {
            imageURL: ""
        }
    },
    created(){
        getDownloadURL(ref(getStorage(), this.$props.member.image)).then((url) => this.imageURL = url)
    },
}
</script>

<style lang="scss">
.member-tile{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    gap: 1em;

    width: fit-content;

    .profile-wrapper{
        background-color: var(--dark);
        width: 7rem;
        height: 7rem;
        overflow: hidden;
        border-radius: 10em;

        .member-image{
            width: 100%;
            // height: 100%;
            background-size: auto;
            background-position: center;
        }

        &:hover{
            cursor: pointer;
            .member-image{
                scale: 1.25;
            }
        }
    }
}

</style>