<template>
<div class="teams-wrapper">
  <section id="teams">
    <h2>
      Our Team
    </h2>
    <div class="leadership-wrapper">
      <h3>
        Leadership
      </h3>
      <div class="leader-wrapper">
        <MemberTile v-for="(member, index) in leadership" :key="index" :member="member"/>
      </div>
    </div>

    <div class="subteams-wrapper">
      <div class="header-wrapper">
        <h3>
          Subteams
        </h3>
        <!-- <span class="learn-more">
          Meet Our Members!
        </span> -->
      </div>
      <div class="team-wrapper">
        <TeamTile v-for="(team, index) in subteams" :key="index" :team="team"/>
      </div>
    </div>
  </section>

</div>
</template>

<script>
import { getDocs, query, where } from "firebase/firestore"
import { members, subteams } from "../firebase"

import MemberTile from '@/components/MemberTile.vue';
import TeamTile from '@/components/TeamTile.vue';

export default {
    name: "TeamsPreview",
    components: {
      MemberTile,
      TeamTile
    },
    data(){
      return {
        leadership: [],
        subteams: []
      }
    },
    async created(){
      const leaders = await getDocs(query(members, where("isLeadership", "==", true)))
      leaders.forEach((ldr) => {
        this.leadership.push(ldr.data())
      })

      const teams = await getDocs(subteams)
      teams.forEach(team => {
        this.subteams.push(team.data())
      })
    },
}
</script>

<style lang="scss">
.teams-wrapper{
  background-color: var(--white);
}
#teams{
  position: relative;

  background-color: var(--white);
  margin: auto;

  padding-top: 12em;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 3em;
  max-width: var(--max-width);
  // height: 100vh;

  h2{
    margin-bottom: 0;
  }

  .leader-wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 5em;
  }

  .subteams-wrapper{
    h3{
      margin-bottom: 0.5em;
    }
  }

  .header-wrapper{
    margin-bottom: 2em;
  }

  .learn-more{
    line-height: 1em;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: fit-content;
    margin: auto;

    &:hover{
      cursor: pointer;
      filter: brightness(0.9);
      text-decoration: underline;
    }

    .side-arrow{
      // width: 1.25em;
      height: 1.25em;
      box-sizing:initial;
      width: fit-content;
      fill: var(--dark);
      margin: auto;
    }
  }

  .team-wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    gap: 5em;
  }
}
</style>