import { info } from "../firebase"
import { getDoc } from "firebase/firestore"
 
import { createStore } from 'vuex'

import temp_data from "@/data/info.json"

export default createStore({
  state: {
    info: {}
  },
  getters: {
    info: state => state.info,
  },
  mutations: {
    setInfo(state, info){
      state.info = info
    },
  },
  actions: {
    async loadInfo({commit}){
      
      await getDoc(info).then((doc) => {
        if(!doc.exists())
        {
          commit("setInfo", temp_data);
          console.log("The document does not exist, return to old data")
          return
        }

        // The data exits
        console.log("Document Data:", doc.data())
        commit("setInfo", doc.data())
      })
    }
  },
  modules: {
  }
})
